import { createStore } from 'vuex';
import axios from 'axios';

const store = createStore({
  state(){
    return {
      name: 'kim',
      age: 20,
      likes: 30,
      clickedLike: false,
      more:{},
    }
  },
  mutations: {
    /* actios 후에 state 변경
    - state변경은 무조건 mutations가 함 */
    setMore(state, data) {
      state.more = data;
    },
    changeName(state) {
      state.name = 'park';
    },
    ageUp(state, data) {
      state.age += data;
    },
    like(state) {
      if (state.clickedLike == false) {
        state.likes++;
        state.clickedLike = true;
      } else {
        state.likes--;
        state.clickedLike = false;
      }
    }
  },
  actions: {
    // - ajax하는 곳
    // - 또는 오래걸리는 작업들
    getData(context) {// actions에 추가한 파라미터(context)는 대충 $store를 뜻함
      axios.get('https://codingapple1.github.io/vue/more0.json')
        .then((a) => {
          context.commit('setMore', a.data)// setMore을 동작시켜서 a.data를 넣어라
      })
    },
  },
})

export default store