<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <p>{{myname}} {{age}} {{likes}}</p>

  <h4>내 나이는 {{$store.state.age}}이란다</h4>
  <button @click="ageUp(10)">나이+1</button>

  <p>{{$store.state.more}}</p>
  <button @click="$store.dispatch('getData')">more</button>

  <Container @write="text = $event" :data="data" :step="step" :image="image" :filterNum="filterNum"/>

  <button @click="more">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        multiple
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>
<script>
import Container from "./components/Container.vue";
import data from "./assets/Data.js";
import axios from "axios";
import {mapMutations, mapState} from 'vuex'

export default {
  name: "App",
  data() {
    return {
      data: data,
      clickedNum: 0,
      step: 0,
      image: '',
      text:'',
      selectedFilter: '',
      counter: 0,
    };
  },
  mounted() {
    this.emitter.on('clickBox', (a)=>{
      this.selectedFilter = a;
    })
  },
  components: {
    Container,
  },

  computed: {
    /* mapState쓰면 state 꺼내쓰는 코드 짧아짐
    그냥 state하나 꺼내쓸 때도 computed안에 사용하면 편할 수도 */
    name(){
      return this.$store.state.name// 꼭 return 적으셈
    },
    // vuex state 한 번에 꺼내쓰려면 ...mapState(['state이름]) or ...mapState({작명:'name'})
    ...mapState(['name', 'age', 'likes']),
    ...mapState({ myname: 'name',}),
  },

  methods: {
    // vuex mutations 한 번에 꺼내쓰려면 ...mapMutations(['함수명'])
    ...mapMutations(['setMore', 'like', 'ageUp']),
    more() {
      axios
        .get(`https://codingapple1.github.io/vue/more${this.clickedNum}.json`)
        .then((result) => {
          console.log(result.data);
          this.data.push(result.data);
        });
      this.clickedNum++;
    },
    upload(e) {
      let file = e.target.files;
      console.log(file);
      let url = URL.createObjectURL(file[0]);
      console.log(url);
      this.image = url;
      this.step++;
    },
    publish() {
      let mypost = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.image,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.text,
        filter: this.selectedFilter,
      };
      
      this.data.unshift(mypost);
      this.step = 0;
    },
  },
};
</script>

