<template>
    <!-- mitt로 데이터 전송하는 법
    1. this.emitter.emit('작명', '데이터')으로 발사하고
    2. this.emitter.on('작명', ()=>{})으로 수신하면 됨 -->
  <div @click="fire" :class="filter + ' filter-item'" :style="`background-image:url(${image})`">
    <slot></slot>
  </div> 
</template>

<script>
export default {
    name: 'FilterBox',
    props: {
        image: String,
        filter: String,
    },
    methods: {
      fire () {
        this.emitter.emit('clickBox', this.filter)
      }
    },
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>